@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap");
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.1/css/all.min.css");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  min-height: 600px;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  color: #f2f2f2;
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("https://source.unsplash.com/random/?city");
  background-repeat: no-repeat !important;
  background-position: center !important;
  background-size: cover !important;
}

.container {
  width: 100%;
  max-width: 500px;
  padding: 30px;
  background: rgba(0, 0, 0, 0.8156862745);
  border-radius: 10px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.8156862745);
}
.container .search {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.container .search .search-field {
  border: none;
  outline: none;
  padding: 8px;
  font-family: inherit;
  font-size: 16px;
  color: #f2f2f2;
  background: rgba(124, 124, 124, 0.168627451);
  width: 100%;
  border-radius: 10px 0 0 10px;
}
.container .search .search-field::placeholder {
  color: #a6a6a6;
}
.container .search .search-btn {
  border: none;
  outline: none;
  padding: 8px;
  font-family: inherit;
  font-size: 16px;
  color: #f2f2f2;
  background: rgba(124, 124, 124, 0.168627451);
  cursor: pointer;
  border-radius: 0 10px 10px 0;
}
.container .weather {
  margin-top: 20px;
}
.container .weather .section-01 {
  margin-bottom: 20px;
}
.container .weather .section-01 .city {
  font-size: 1.5rem;
  line-height: 1.6;
}
.container .weather .section-01 .date {
  font-size: 0.75rem;
  color: #a6a6a6;
}
.container .weather .section-02 {
  margin-bottom: 20px;
}
.container .weather .section-02 .temp {
  font-size: 2rem;
  font-weight: 500;
}
.container .weather .section-02 .icon-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.container .weather .section-02 .icon-wrapper .description {
  margin-left: 5px;
  text-transform: uppercase;
}
.container .weather .section-02 .temp-min-max,
.container .weather .section-02 .feels {
  font-size: 0.75rem;
  color: #a6a6a6;
}
.container .weather .section-03 {
  font-size: 0.9375rem;
  color: #bfbfbf;
}
.container .weather .section-03 .help-text {
  margin-top: 0.75rem;
  font-size: 0.6875rem;
  color: #a6a6a6;
}
.container .weather.loading {
  visibility: hidden;
  overflow: hidden;
  max-height: 30px;
  position: relative;
}
.container .weather.loading::after {
  visibility: visible;
  content: "Loading...";
  color: #f2f2f2;
  position: absolute;
  top: 0;
  left: 10px;
}